@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
:root{--color-purple: #639;--color-pink: #f6c;--color-yellow: gold;--color-orange: #f96;--color-green: #6fc;--color-green-rgb: #6fc;--color-text: #ccc;--color-text-dark: #0c0c0c;--color-link: #000;--color-link-hover: #000;--color-foreground: #ccc;--color-background: gold;--color-background-dark: #0c0c0c;--hero-background-color: #0c0c0c;--font-family: 'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{margin:0;font-size:1.15rem;font-family:'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;font-family:var(--font-family);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{color:#000;color:var(--color-link);text-decoration:none;-webkit-transition:color 0.5s;transition:color 0.5s}a:hover{color:#000;color:var(--color-link-hover)}h1,h2,h3,h4,h5,h6{line-height:2;font-weight:400;color:#0c0c0c;color:var(--color-text-dark)}h1{font-size:1.5rem}p,li{line-height:2}

