:root {
  /* generic colors */
  $color-gray: #cccccc;
  $color-dark-gray: #0c0c0c;
  // $color-purple: #8374eb;
  // $color-pink: #e874eb;
  // $color-yellow: #ecd555;
  // $color-orange: #eca474;
  // $color-green: #74ec95;
  $color-purple: rebeccapurple;
  $color-pink: #ff66cc;
  $color-yellow: gold;
  $color-orange: #ff9966;
  $color-green: #66ffcc;
  $color-green-rgb: rgba($color-green, 1);
  /* behavior colors */
  $color-text: $color-gray;
  $color-text-dark: $color-dark-gray;
  $color-foreground: $color-gray;
  $color-background: $color-yellow;
  $color-background-dark: $color-dark-gray;
  /* CSS variables */
  --color-purple: #{$color-purple};
  --color-pink: #{$color-pink};
  --color-yellow: #{$color-yellow};
  --color-orange: #{$color-orange};
  --color-green: #{$color-green};
  --color-green-rgb: #{$color-green-rgb};
  --color-text: #{$color-text};
  --color-text-dark: #{$color-text-dark};
  --color-link: #{darken($color-purple, 40%)};
  --color-link-hover: #{darken($color-purple, 75%)};
  --color-foreground: #{$color-foreground};
  --color-background: #{$color-background};
  --color-background-dark: #{$color-background-dark};
  --hero-background-color: #{$color-dark-gray};
  --font-family: 'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
}
